html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: "Roboto", sans-serif;
  font-size: 1.6rem;
}
body.fijar-body {
  overflow: hidden;
}
body.body-scroll {
  padding-top: 8.6rem;
}

p {
  font-size: 1.5rem;
  color: #000000;
  line-height: 1.5;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

h1, h2, h3 {
  margin: 0 0 5rem 0;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
}

img {
  max-width: 100%;
  width: 100%;
  height: 100%;
  display: block;
}

.default-img {
  margin: 0 auto;
  z-index: 10;
  min-width: 30rem;
}

.default-contenedor {
  max-width: 100%;
  margin: 0 auto;
  max-height: 50rem;
  overflow: hidden;
}

.header {
  background-color: #1ab5e6;
  display: block;
}

/* SocialIcons Personalizado para el header */
.header__socialIcons {
  max-width: 40rem;
  margin: 0 auto;
  display: flex;
  justify-content: space-around;
  z-index: 5;
}
@media (min-width: 768px) {
  .header__socialIcons {
    margin-right: 13%;
    padding-bottom: 2rem;
  }
}

.header__socialIcons__icons {
  transition-property: top;
  transition-duration: 0.2s;
  transition-timing-function: ease-in-out;
  position: relative;
  top: 0;
  color: #ebc246;
}
.header__socialIcons__icons:hover {
  top: -7px;
  transition-property: top;
  transition-duration: 0.2s;
  transition-timing-function: ease-in-out;
}

.social-icon {
  color: #ebc246;
}

.nav_menu-active {
  font-weight: bold;
  text-decoration: underline;
}

.header__nav {
  /* position: fixed; */
  width: 100%;
  display: flex;
  background-color: #1ab5e6;
  color: #FFFFFF;
  justify-content: center;
  padding-top: 2.5rem;
  height: 6rem;
  margin: 0 0 1rem 0;
}
.header__nav .header__web-tittle {
  display: none;
  font-size: 2.8rem;
  padding-left: 10rem;
  margin: 0;
  color: #ebc246;
}
@media (min-width: 768px) {
  .header__nav .header__web-tittle {
    display: block;
  }
}
@media (min-width: 768px) {
  .header__nav {
    display: grid;
    grid-template-columns: 1fr 1fr;
  }
}
.header__nav nav {
  display: flex;
  gap: 2rem;
  justify-content: center;
  font-size: 1.4rem;
  z-index: 1;
}
@media (min-width: 768px) {
  .header__nav nav {
    grid-column: 2/3;
    gap: 4rem;
    justify-content: left;
  }
}
.header__nav nav a {
  color: #FFFFFF;
  text-transform: uppercase;
  transition-property: color;
  transition-duration: 1s;
  transition-timing-function: ease-out;
}
.header__nav nav a:hover {
  cursor: pointer;
  transition-property: color;
  transition-duration: 1s;
  transition-timing-function: ease-out;
  color: #ebc246;
}

@media (max-width: 480px) {
  .header__nav {
    padding-top: 0.5rem;
  }
  .header__nav nav {
    /* display: grid;
    grid-template-columns: repeat(2,1fr);
    grid-template-rows: 2; */
    height: 7rem;
    align-items: center;
    justify-items: center;
    gap: 1rem;
  }
}
.nosotros-img {
  margin: 0 auto;
  z-index: 10;
  height: 100%;
  width: 100%;
  outline: none;
  padding: 20rem;
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
  filter: brightness(30%);
  /* filter:opacity(30%); */
}

.nosotros-contenedor {
  max-width: 100%;
  margin: 0 auto;
  max-height: 50rem;
  overflow: hidden;
  z-index: 11;
}

.inicio-img {
  display: block;
  height: 100%;
  width: 100%;
  outline: none;
  padding: 15rem;
  /* max-height: 30rem; */
  margin: 2rem auto 0 auto;
  z-index: 1;
  background-repeat: no-repeat;
  background-position: center center;
  background-size: contain;
}
@media (min-width: 1200px) {
  .inicio-img {
    background-size: cover;
    max-width: 80%;
  }
}

.inicio-contenedor {
  max-width: 100%;
  margin: 0 auto;
  background-color: #1ab5e6;
}

.inicio_info1 {
  display: flex;
  flex-direction: column;
  width: 100%;
  padding: 1rem;
}
.inicio_info1 h1 {
  text-transform: uppercase;
  font-weight: 700;
  text-align: center;
  margin-bottom: 0;
  font-size: 2.5rem;
  margin-top: 2rem;
}
@media (min-width: 768px) {
  .inicio_info1 h1 {
    font-size: 4rem;
    margin-top: 7rem;
  }
}
.inicio_info1 h3 {
  text-transform: uppercase;
  margin-bottom: 0;
  margin-top: 12rem;
}
.inicio_info1 P {
  padding-left: 2rem;
}

.inicio_info1_servicios-cards {
  display: grid;
  padding: 1rem;
}
@media (min-width: 768px) {
  .inicio_info1_servicios-cards {
    grid-template-columns: repeat(3, 1fr);
    gap: 2.5rem;
  }
}

.info-card {
  display: flex;
  flex-direction: column;
  align-items: center;
  transition: transform 0.2s;
}
.info-card:hover {
  transform: scale(1.03);
}
.info-card .info-card_icon {
  color: #1ab5e6;
}
.info-card .info-card_titulo {
  text-align: center;
}
.info-card .info-card_titulo h2 {
  font-size: 1.7rem;
  font-weight: 700;
  margin-bottom: 0;
}
.info-card .info-card_texto p {
  font-size: 1.4rem;
  line-height: 1.7;
}